import { Form } from './Form';
export class ContactsForm extends Form {
    getEventName() {
        return 'contacts';
    }
    constructor(template, events) {
        super(template, events);
        this.inputEmail = this.formElement.querySelector('input[name="email"]');
        this.inputPhone = this.formElement.querySelector('input[name="phone"]');
        this.inputEmail.addEventListener('input', () => {
            this.events.emit('contacts:setEmail', { email: this.inputEmail.value });
        });
        this.inputPhone.addEventListener('input', () => {
            this.events.emit('contacts:setPhone', { phone: this.inputPhone.value });
        });
    }
    handleSubmit() {
        this.events.emit('contacts:submit');
    }
    render() {
        return super.render();
    }
}
