import { createElement, cloneTemplate, ensureElement } from '../../utils/utils';
export class Basket {
    constructor(template, events) {
        this.events = events;
        this.basket = cloneTemplate(template);
        this.title = ensureElement('.modal__title', this.basket);
        this.basketList = ensureElement('.basket__list', this.basket);
        this.basketPrice = ensureElement('.basket__price', this.basket);
        this.button = ensureElement('.basket__button', this.basket);
        this.button.addEventListener('click', () => {
            this.events.emit('order:open');
        });
        this.items = [];
    }
    set items(items) {
        if (items.length > 0) {
            this.basketList.replaceChildren(...items);
            this.button.removeAttribute('disabled');
        }
        this.basketList.replaceChildren(createElement('p', { textContent: 'Корзина пуста' }));
        this.button.setAttribute('disabled', 'disabled');
    }
    renderTotalSumProduct(totalSum) {
        this.basketPrice.textContent = `${totalSum} синапсов`;
    }
    render() {
        return this.basket;
    }
}
