import { ensureElement } from "../../utils/utils";
export class Page {
    constructor(events, coutaiter) {
        this.events = events;
        this.basketButton = ensureElement('.header__basket', coutaiter);
        this.basketCouter = ensureElement('.header__basket-couter', coutaiter);
        this.pageWrapper = ensureElement('.page__wrapper', coutaiter);
        this.gallery = ensureElement('.gallery', coutaiter);
        this.basketButton.addEventListener('click', () => {
            this.events.emit('basket:open');
        });
        this.events.on('basket:updated', (basket) => {
            this.renderBasketCounter(basket.length);
        });
    }
    renderBasketCounter(value) {
        this.basketCouter.textContent = String(value);
    }
    set locked(value) {
        if (value) {
            this.pageWrapper.classList.add('page__wrapper_locked');
        }
        else {
            this.pageWrapper.classList.remove('page__wrapper_locked');
        }
    }
    appendCard(element) {
        this.gallery.appendChild(element);
    }
}
