import { ensureElement } from '../../utils/utils';
export class Render {
    constructor() { }
    initElements(context) {
        this.title = ensureElement('.card__title', context);
        this.price = ensureElement('.card__price', context);
    }
    setPrice(value) {
        return value === null ? 'Бесценно' : `${value} синапсов`;
    }
    renderTitle(title) {
        this.title.textContent = title;
    }
    renderPrice(price) {
        this.price.textContent = this.setPrice(price);
    }
}
