import { ensureElement, cloneTemplate } from "../../utils/utils";
export class Success {
    constructor(template, events) {
        this.events = events;
        this.successElement = cloneTemplate(template);
        this.messageElement = ensureElement('.order-success__description', this.successElement);
        this.button = ensureElement('.order-success__close', this.successElement);
        this.button.addEventListener('click', () => {
            this.events.emit('success:close');
        });
    }
    render(total) {
        this.messageElement.textContent = `Списано ${total} синапсов`;
        return this.successElement;
    }
}
