import { ensureAllElements, ensureElement, cloneTemplate, } from '../../utils/utils';
export class Form {
    constructor(template, events) {
        this.events = events;
        this.formElement = cloneTemplate(template);
        this.InputAll = ensureAllElements('.form__input', this.formElement);
        this.buttonAll = ensureAllElements('.button_alt', this.formElement);
        this.buttonSubmit = ensureElement('.button', this.formElement);
        this.formErrors = ensureElement('.form__errors', this.formElement);
        this.addInputListener();
        this.addSubmitListener();
    }
    addInputListener() {
        this.InputAll.forEach((input) => {
            input.addEventListener('input', () => {
                this.onInputChange();
            });
        });
    }
    addSubmitListener() {
        this.formElement.addEventListener('submit', (ev) => {
            ev.preventDefault();
            this.handleSubmit();
        });
    }
    onInputChange() {
        this.events.emit(`${this.getEventName()}:changed`);
    }
    set isValid(value) {
        this.buttonSubmit.disabled = !value;
    }
    displayErrors(errors) {
        this.formErrors = ensureElement('.form__errors', this.formElement);
        this.formErrors.innerHTML = '';
        Object.values(errors).forEach((err) => {
            const errorElement = document.createElement('p');
            errorElement.textContent = err;
            this.formElement.appendChild(errorElement);
        });
    }
    render() {
        return this.formElement;
    }
}
